import React, { useContext, useMemo } from "react";
import { Typography, Grid, Stack, Slider, Divider } from "@mui/joy";
import { GET_ALL_VALIDATION_T } from "../../types";
import TableCustom from "../../components/TableCustome";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from "chart.js";
import { green, grey, red } from "@mui/material/colors";
import formatDateToDDMMYYYY from "../../utils/formatDateToDDMMYYYY";
import { getPrixProduitMarcheParRegion } from "../../utils/getPrixProduitMarcheParRegion";
import { CardMedia } from "@mui/material";
import PageLooperContext from "../../providers/PageLooperContext";
import getProduitImageUrl from "../../utils/getProduitImageUrl";

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

// Définir la nouvelle interface pour les données entrantes du composant
interface MarcheCollectes {
    marche: string;
    collectes: GET_ALL_VALIDATION_T[];
}

const PageContentProduitRegion: React.FC<{
    produit: string;
    region: string;
    data: MarcheCollectes[]; // La donnée reçue est un tableau de MarcheCollectes
}> = ({ produit, region, data }) => {
    const { apiData } = useContext(PageLooperContext);

    // Utiliser la fonction utilitaire pour traiter les données et obtenir les informations nécessaires
    const processedData = getPrixProduitMarcheParRegion(data);

    // Trier les marchés pour l'affichage
    const marches = processedData.map((p) => p.marche);

    // Préparer les lignes du tableau avec les données traitées, en incluant les "gaps"
    const tableRows = [
        {
            label: "Prix actuel",
            values: processedData.map((p) =>
                p.prixActuel !== null ? Math.round(p.prixActuel).toLocaleString() + " FCFA" : "-"
            ),
            center: true
        },
        {
            label: "Date actuelle",
            values: processedData.map((p) => formatDateToDDMMYYYY(p.dateActuelle) || "-"),
        },
        {
            label: "Nbr de collectes actuel",
            values: processedData.map((p) => (p.nbrCollecteActuel !== null ? p.nbrCollecteActuel.toString() : "-")),
        },
        'gap', // Ajout d'une ligne de séparation après les données actuelles
        {
            label: "Prix précédent",
            values: processedData.map((p) =>
                p.prixPrecedent !== null ? Math.round(p.prixPrecedent).toLocaleString() + " FCFA" : "-"
            ),
        },
        {
            label: "Date précédente",
            values: processedData.map((p) => formatDateToDDMMYYYY(p.datePrecedente) || "-"),
        },
        {
            label: "Nbr de collectes précédent",
            values: processedData.map((p) => (p.nbrCollectePrecedente !== null ? p.nbrCollectePrecedente.toString() : "-")),
        },
        'gap', // Ajout d'une ligne de séparation après les données précédentes
        {
            label: "Evolution %",
            values: processedData.map((p) => {
                if (p.evolution === null) return "-";
                const arrow = p.evolution > 0 ? "⬆️" : p.evolution < 0 ? "⬇️" : "";
                return `${arrow} ${Math.abs(p.evolution).toFixed(2)}%`;
            }),
        },
    ];

    const codeProduit = useMemo(() => (apiData.find((p) => p.produit === produit)?.codeProduit), [apiData, produit])


    return (
        <Stack sx={{ gap: 3, p: 3, }} height={'100%'}>
            <Typography
                level="h4"
                fontSize={"2vw"}
                textColor={grey[700]}
                fontWeight={300}
            >
                Prix de
                {` `}<Typography
                    fontWeight={700}
                    textColor={green[900]}
                >{produit}</Typography> {` `}
                dans la région de
                {` `}
                <Typography
                    fontWeight={700}
                    textColor={red[900]}
                >{region}</Typography>
            </Typography>

            <Grid container spacing={5} height={'100%'} flex={1} >
                <Grid xs={12} md={9}>
                    <TableCustom
                        columns={[
                            { label: "#", key: "label" },
                            ...marches.map((m) => ({ label: m, key: m, center: true })),
                        ]}
                        data={tableRows.map((row) => {
                            if (row === 'gap') {
                                return 'gap'; // Retourne la chaîne de caractères si c'est un "gap"
                            }

                            //@ts-ignore
                            const obj: any = { label: row.label };
                            //@ts-ignore
                            row.values.forEach((v, i) => {
                                obj[marches[i]] = <b>{v}</b>;
                            });
                            return obj;
                        })}
                    />
                </Grid>

                <Grid xs={12} md={3}  >
                    <CardMedia
                        component={'img'}
                        src={getProduitImageUrl(codeProduit || '')}
                    // sx={{ flex:1, maxHeight:'40%' }}
                    />

                    <Stack
                        mt={2}
                    >
                        <Typography level="h3" fontSize={"1.5vw"}>
                            Points à retenir :
                        </Typography>

                        <Divider
                            sx={{
                                width: 50,
                                height: 10,
                                borderRadius: 50,
                                bgcolor: green[800]
                            }}
                        />

                        <Stack
                            component="ul"
                            mt={2}
                            fontSize="0.9vw"
                            gap={1}
                        >
                            {processedData.length > 1 && (
                                <li>
                                    Le prix de l'oignon est relativement plus bas dans le marché de{" "}
                                    {
                                        processedData.reduce((prev, cur) =>
                                            cur?.prixActuel !== null && cur?.prixActuel < (prev?.prixActuel ?? Infinity) ? cur : prev
                                        ).marche
                                    }.
                                </li>
                            )}

                            {processedData.map((p, i) => {
                                if (p.prixActuel === null || p.prixPrecedent === null) return null;

                                const difference = p.prixActuel - p.prixPrecedent;
                                const tendance = difference > 0 ? "augmenté" : difference < 0 ? "diminué" : "resté stable";
                                const valeurAbs = Math.abs(difference).toFixed(0).toLocaleString();

                                return (
                                    <li key={i}>
                                        Dans le <b>{p.marche}</b>,
                                        {p.nbrCollecteActuel && p.nbrCollecteActuel > 1
                                            ? <> sur une collecte dans <b>{p.nbrCollecteActuel}</b> marchés, </>
                                            : " "}
                                        le produit <b>{produit}</b> a vu son prix {tendance}
                                        {difference !== 0 && <> de <b>{valeurAbs} FCFA</b></>}
                                        par rapport à la dernière collecte du{" "}
                                        <b>{formatDateToDDMMYYYY(p.datePrecedente)}</b>.
                                    </li>
                                );
                            })}
                        </Stack>
                    </Stack>
                </Grid>

            </Grid>
        </Stack>
    );
};


export default PageContentProduitRegion;